	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Quick Links","date":"29 Apr 2003","slug":"/2003-04-29-quick-links/","tags":"Y2003"},"html":"<p>You may have noticed that I've been having fun the past fews days with quick links in my sidebar, a l치 <a title=\"anil dash \" href=\"http://dashes.com/anil/\">Anil Dash</a>. It's a way I can archive a link and snap off a comment or two without taking up a lot of column inches (vertical pixels?) on the home page. Technically, I set up a second blog for the quick links with a very simple index page, which gets included here via php.</p>\n<p>Now I'm not sure if the sidebar is the right place to put them, seeing as there will (hopefully) be a lot more of them than regular posts. One option is to aggregate them into a single post of their own each day (a l치 <a href=\"http://www.decafbad.com/blog/links/aoeabaofheecf.html\">0xDECAFBAD<a> or <a href=\"http://diveintomark.org/archives/2003/04/23/in_brief_insomniac_edition.html\">diveintomark</a>). But I don't want to break the flow, if you know what I mean. The other questions are whether I should ping weblogs.com when I add a quick link (probably not) or whether I should include them in my RSS feed (maybe).</a></a></p>"},"next":{"fields":{"title":"Old Enough To Know Better","date":"29 Apr 2003","slug":"/2003-04-29-old-enough-to-know-better/"}},"previous":{"fields":{"title":"U.S. Troops Fire on Iraqi Protesters, Leaving 15 Dead","date":"29 Apr 2003","slug":"/2003-04-29-us-troops-fire-on-iraqi-protesters-leaving-15-dead/"}}},{"node":{"fields":{"title":"Old Enough To Know Better","date":"29 Apr 2003","slug":"/2003-04-29-old-enough-to-know-better/","tags":"Y2003"},"html":"<p>Some things are better left to professionals. Right now I wish potty training could be one of them.</p>\n<p>In the presence of a professional child care provider, Christopher is able to go without a diaper the entire day. Then his amatuer child care father takes him home. Despite denying repeatly that he needs to use the toilet, Christopher walks in the door, pulls down his pants, marches into the living room and proceeds to piss on his play carpet.</p>\n<p>He has the ability. He has the confidence. He even has the stamina. He just doesn't have the desire. And, I suspect, he wanted to piss his father off.</p>\n<p>Good thing the carpet is 100% polyester.</p>"},"next":{"fields":{"title":"Getting Entropy","date":"29 Apr 2003","slug":"/2003-04-29-getting-entropy/"}},"previous":{"fields":{"title":"Quick Links","date":"29 Apr 2003","slug":"/2003-04-29-quick-links/"}}},{"node":{"fields":{"title":"Getting Entropy","date":"29 Apr 2003","slug":"/2003-04-29-getting-entropy/","tags":"Y2003"},"html":"<p><a title=\"To install Apache 2 with SSL on Solaris, you need this\" href=\"http://www.sunfreeware.com/openssh26-7.html#ent\">Sunfreeware: OpenSSH  - getting entropy</a></p>"},"next":{"fields":{"title":"Berlin faces violent 1 May","date":"29 Apr 2003","slug":"/2003-04-29-berlin-faces-violent-1-may/"}},"previous":{"fields":{"title":"Old Enough To Know Better","date":"29 Apr 2003","slug":"/2003-04-29-old-enough-to-know-better/"}}}],"pathPrefix":"","first":false,"last":false,"index":763,"pageCount":1101}}

